}  

body {
  margin: 0;
  font-family: Helvetica;
  color: #00120B;
  font-size:12px;
}

.bgOverlay {
  min-height: 100vh;
  height: 100%;
  width: 100%;
  background-color: white;
}
h1, h2, h3, h5, h6, .MuiTypography-h5-82{
  font-family: UnicaOne !important;
  text-transform: uppercase !important;
}

.some-page-wrapper {
  margin: 15px;
  width: 100%;
  background-color: white;
}
.canvasjs-chart-credit {
  display: none !important;
}
#logoDiv {
  text-align: start;
  position: absolute;
  width: 40px;
}
#logoDiv img {
  width: 100%;
}
input,
select {
  cursor: pointer;
}

input[type="text"] {
  cursor: text;
}

.loadingGif {
  display: flex;
  align-items: center;
  justify-content: center;
}

.loadingGif p {
  margin: 0px;
  padding: 10px;
}

.searchBarText {
  display: flex;
  align-items: center;
  /* justify-content: ce⁄nter; */
  padding-left: 10px;
  padding-bottom: 10px;
  /* max-width: 400px; */
}
.searchBarText input {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-height: 20px;
  max-width: 100px;
  margin: 0px;
  margin-right: 20px;
}
.searchBarText input[type="text"],
input[type="search"] {
  max-width: 300px;
  min-width: 300px;
  font-size: 12px;
  cursor: text;
}
div.instruction {
  width: 500px;
  text-align: justify;
}

.jss1 {
  overflow: scroll !important;
  background-color: transparent !important;
}
.clicked {
  text-decoration: underline;
  cursor: pointer;
}
.clicked:hover {
  opacity: 0.3;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
}

.double-column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 2 1;
}

.error {
  /* background-color: #35605A; */
  word-wrap: break-word;
  border-radius: 15px;
  padding: 8px;
  margin: 8px;
}

label {
  padding-right: 2px;
}

.sidebar {
  padding-left: 10px;
  padding-right: 10px;
  overflow-x: hidden;
}
.sidebar .line {
  border-bottom: 1px solid;
  padding-top: 5px;
  margin-bottom: 10px;
}

.sidebar .input {
  padding-bottom: 10px;
}

.cards-flex {
  width: 90%;
  /* text-align: center; */
  align-items: baseline;
}

.card {
  display: inline-block;
  margin: 6px;
  max-width: 180px;
  height: 250px;
}
.MuiCard-root-1 {
  overflow: scroll !important;
  background-color: transparent !important;
}

.content {
  padding-left: 10px;
}

@media (max-width: 1000px) {
  .content {
    position: relative !important;
    left: 0% !important;
    width: calc(100% - 10px) !important;
    padding-left: 10px;
  }
}

.words {
  color: #35605a;
  margin-bottom: 10px;
  display: flex;
  justify-content: flex-end;
  word-break: break-all;
  align-items: center;
}

.words button {
  margin-left: 5px;
}

.topicResul {
  width: 100%;
  overflow-y: hidden;
  white-space: normal;
  padding-bottom: 20px;
  border-right: 20px;
}

.wordData {
  display: inline-block;
  padding-left: 10px;
  min-width: 200px;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.sidebar {
  text-align: center;
}

.input {
  width: 100%;
  text-align: center;
  margin-top: 5px;
}

.form label {
  word-wrap: break-word;
}
.button {
  width: 80%;
  align-content: center;
  padding: 6px;
  border-radius: 10px;
  margin: 10px auto;
  display: grid;
  cursor: pointer;
}

.button:hover {
  background-color: #35605a;
  border-color: #35605a;
  color: white;
}
.searchButtonDiv {
  width: 300px;
}

.searchButton {
  width: 100%;
  align-content: center;
  padding: 6px;
  border-radius: 10px;
  margin: 10px auto;
  display: grid;
  cursor: pointer;
  margin-right: 20px;
  margin-left: 10px;
}

.searchButton:hover {
  background-color: #35605a;
  border-color: #35605a;
  color: white;
}

.searchButton:disabled {
  background-color: #d8e4ff;
  border-color: #d8e4ff;
  color: white;
  cursor: not-allowed;
}

.form .input input {
  float: right;
  margin-right: 10px;
  width: calc(50% - 50px);
}

.form select {
  height: 30px;
  margin-left: 20px;
  max-width: 150px;
  margin-top: 10px;
  margin-bottom: 5px;
}

.button:disabled {
  background-color: #d8e4ff;
  border-color: #d8e4ff;
  color: white;
  cursor: not-allowed;
}

input {
  border-radius: 5px;
  padding-left: 10px;
}

.deleteButton {
  background-color: white !important;
  border-radius: 10px !important;
  display: inline !important;
  padding: 0 !important;
  min-width: 30px !important;
  align-items: flex-end;
}

.deleteButton:hover {
  background-color: #35605a !important;
  border-color: #35605a !important;
  color: white !important;
}

/* width */
::-webkit-scrollbar {
  /* width: 10px; */
  display: none;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent !important;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #d8e4ff !important;
  border-radius: 35px !important;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #d8e4ff !important;
}

::-webkit-scrollbar-corner {
  width: 0px;
  background: transparent !important;
}

table th div.text {
  max-height: 200px;
  overflow-y: auto;
  text-align: justify;
  padding: 10px;
}
table th div.text div {
  display: inline-block;
  margin-right: 6px;
}

table th div.tags {
  text-align: start;
}

table th div.tags div.tag {
  display: inline-flex;
  word-wrap: break-word;

  margin: 4px;
  padding: 4px;
  border-radius: 5px;
  border: solid 3px #dee2e6;
}
table th div.tags div.tag div {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3px;
}
table th div.tags div.tag .tagType {
  background-color: rgb(158, 146, 164);
  color: white;
  margin-right: 6px;
  border-radius: 5px;
}

table th div.text div.red {
  background-color: red;
}
table th div.text div.green {
  background-color: greenyellow;
}

table th {
  border-left: 2px solid #dee2e6;
  border-bottom: 2px solid #dee2e6;
  padding: 3px;
  font-weight: normal;
  font-weight: initial;
}

table th:last-child {
  border-right: 2px solid #dee2e6;
}

table tr.hoverRows:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

thead tr:first-child th {
  /* padding: 8px; */
  border-top: 2px solid #dee2e6;
}
table {
  border-spacing: 0px;
  padding: 10px;
  width: 90%;
  min-width: 200px;
}

table .button {
  margin: auto;
  width: unset;
}

table thead {
  background-color: white;
}

table thead {
  background-color: rgba(0, 0, 0, 0.1);
}
table thead tr:nth-child(1) {
  background-color: rgba(0, 0, 0, 0.1);
}

.actionButton .button {
  width: 80px;
  margin-bottom: 10px;
  margin-top: 10px;
}

.worcCloud{
  display: flex;
  padding-left: 10px;
}
.select-search-box {
  width: 325px;
  position: relative;
  /* margin-bottom: 20px; */
  background: #fff;
  padding-left: 10px;
  border-radius: 4px;
  padding-bottom: 10px;
}

.select-search-box--multiple {
  /* border-radius: 4px; */
  /* box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1),
    0 3px 6px 0 rgba(0, 0, 0, 0.07); */
  /* overflow: hidden; */
}


.select-search-box__out {
  /* display: none; */
}

.select-search-box__search {
  display: block;
  min-width: 315px !important;
  width: 315px !important;
  height: 26px !important;
  /* border: none; */
  background: none;
  /* outline: none; */
  padding: 1px 1px 1px 10px;
  color: #222f3e;
  /* -webkit-appearance: none; */
  /* box-sizing: border-box; */
  position: relative;
  z-index: 2;
  /* box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1); */
}


.select-search-box__select {
  display: none;
  position: absolute;
  top: 30px;
  left: 10px;
  width: calc(100% - 20px);
  right: 0;
  background: #fff;
  border-radius: 4px;
  overflow: auto;
  box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1);
  z-index: 100;
  min-height: 100px;
}

.select-search-box--multiple .select-search-box__select {
  display: block;
  position: static;
  border-top: 1px solid #eee;
  border-radius: 0;
  box-shadow: none;
}

.select-search-box__select--display {
  display: block;
}

.select-search-box__option {
  font-size: 16px;
  font-weight: 400;
  color: #616b74;
  padding: 15px 20px;
  border-top: 1px solid #eee;
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.select-search-box__options{
  padding: 10px;
  /* padding-left: 20px; */
}
.select-search-box__option:first-child {
  border-top: none;
}

.select-search-box__option--hover,
.select-search-box__option:hover {
  background: #f4f7fa;
}

.select-search-box__option--selected {
  background: #54a0ff;
  color: #fff;
  border-top-color: #2184ff;
}

.select-search-box__option--selected:hover,
.select-search-box__option--selected.select-search-box__option--hover {
  background: #2184ff;
  color: #fff;
  border-top-color: #2184ff;
}

.select-search-box__group {
  /* margin-top: 20px; */
  /* padding-top: 20px; */
  border-top: 1px solid #eee;
  position: relative;
}

.select-search-box__group-header {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: white;
  padding: 0 10px;
  color: rgba(0, 0, 0, 0.5);
  font-size: 12px;
}

.divTableDocuments{
  min-height: 200px;
}
