.select-search-box {
  width: 325px;
  position: relative;
  /* margin-bottom: 20px; */
  background: #fff;
  padding-left: 10px;
  border-radius: 4px;
  padding-bottom: 10px;
}

.select-search-box--multiple {
  /* border-radius: 4px; */
  /* box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1),
    0 3px 6px 0 rgba(0, 0, 0, 0.07); */
  /* overflow: hidden; */
}


.select-search-box__out {
  /* display: none; */
}

.select-search-box__search {
  display: block;
  min-width: 315px !important;
  width: 315px !important;
  height: 26px !important;
  /* border: none; */
  background: none;
  /* outline: none; */
  padding: 1px 1px 1px 10px;
  color: #222f3e;
  /* -webkit-appearance: none; */
  /* box-sizing: border-box; */
  position: relative;
  z-index: 2;
  /* box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1); */
}


.select-search-box__select {
  display: none;
  position: absolute;
  top: 30px;
  left: 10px;
  width: calc(100% - 20px);
  right: 0;
  background: #fff;
  border-radius: 4px;
  overflow: auto;
  box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1);
  z-index: 100;
  min-height: 100px;
}

.select-search-box--multiple .select-search-box__select {
  display: block;
  position: static;
  border-top: 1px solid #eee;
  border-radius: 0;
  box-shadow: none;
}

.select-search-box__select--display {
  display: block;
}

.select-search-box__option {
  font-size: 16px;
  font-weight: 400;
  color: #616b74;
  padding: 15px 20px;
  border-top: 1px solid #eee;
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  user-select: none;
}
.select-search-box__options{
  padding: 10px;
  /* padding-left: 20px; */
}
.select-search-box__option:first-child {
  border-top: none;
}

.select-search-box__option--hover,
.select-search-box__option:hover {
  background: #f4f7fa;
}

.select-search-box__option--selected {
  background: #54a0ff;
  color: #fff;
  border-top-color: #2184ff;
}

.select-search-box__option--selected:hover,
.select-search-box__option--selected.select-search-box__option--hover {
  background: #2184ff;
  color: #fff;
  border-top-color: #2184ff;
}

.select-search-box__group {
  /* margin-top: 20px; */
  /* padding-top: 20px; */
  border-top: 1px solid #eee;
  position: relative;
}

.select-search-box__group-header {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 0 10px;
  color: rgba(0, 0, 0, 0.5);
  font-size: 12px;
}

.divTableDocuments{
  min-height: 200px;
}